export const CardContentType = {
  cardPhoto: "card-photo",
  cardIllustration: "card-illustration",
  cardIcon: "card-icon"
} as const;

export type CardContentType = typeof CardContentType[keyof typeof CardContentType]

export const ButtonPosition = {
  horizontalFill: "horizontal-fill",
  verticalFill: "vertical-fill",
  horizontalHug: "horizontal-hug",
  verticalHug: "vertical-hug",
} as const;

export type ButtonPosition = typeof ButtonPosition[keyof typeof ButtonPosition]

export interface CardContentStatus {
  type?: 'icon-left' | 'icon-right' | 'only' | 'simple';
  color?: 'alba' | 'andino' | 'caribe' | 'macondo' | 'flamenco' | 'orquidea' | 'neutro' | 'black' | 'white';
  border?: 'center' | 'left' | 'right';
  icon?: string;
  text?: string;
  widthTag?: string;
}
export interface CardContentLink {
  id: string;
  typeLink?: 'simple' | 'underline';
  sizeLink?: 'default' | 'small' | 'large';
  class?: string;
  text?: string;
  url?: string;
  iconLeft?: string;
  iconRight?: string;
}
export interface CardContentButton {
  id: string;
  type: 'primary' | 'secondary';
  sizeButton: 'small';
  icon?: string;
  text?: string;
  event?: any;
}

interface Options {
  id?: string;
  iconLeft?: string;
  iconRight?: string;
  mainText?: string;
  secondaryText?: string;
  imagRight?: string;
  imagLeft?: string;
  altImg?: string;
}
export interface CardContentIconButton {
  options?: Options[];
}
export interface CardContentText {
  text?: string;
  classText?: string;
}
export interface BcCardContentConfig {
  idCard: string;
  cardPosition: "horizontal" | "vertical";
  cardSize?: "small" | "medium";
  cardType: CardContentType;
  icon?: string;
  iconFloat?: string;
  urlImage?: string;
  title?: CardContentText;
  subtitle?: CardContentText;
  text?: CardContentText;
  additionalText?: CardContentText;
  additionalIcon?: string;
  componentStatus?: CardContentStatus;
  componentButtonOne?: CardContentButton;
  componentButtonTwo?: CardContentButton;
  componentButtonPosition?: ButtonPosition;
  componentIconButton?: CardContentIconButton;
  componentLink?: CardContentLink;
}
